import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import firebase from 'firebase';

import Title from './components/Title';
import Task from './components/Task';
// import tasks from './task';
import Form from './components/Form';


const config = {
    apiKey: "AIzaSyCzteG0bEqmXtF2kH_YcQjai3htRPm9fu4",
    authDomain: "to-do-list-52870.firebaseapp.com",
    databaseURL: "https://to-do-list-52870.firebaseio.com/",
    projectId: "to-do-list-52870"
}

const fbase = firebase.initializeApp(config);

class App extends Component {

  constructor(props) {
    super(props);


    this.state = {
      tasks: []
    };

    this.handleStatusChange = this.handleStatusChange.bind(this);
    this.deleteTask = this.deleteTask.bind(this);
    this.addTask = this.addTask.bind(this);
    this.nextId = this.nextId.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
  }

  componentDidMount() {

    fbase.database().ref().child("tasks").on('value', data => {
      const tasks = data.val();
      const tasksArr = [];

      for(let task in tasks) {
        tasksArr.push(tasks[task]);
      }

      const lastTask = tasksArr.length - 1;

      lastTask >= 0 ? this._nextId = tasksArr[lastTask].id : this._nextId = 0;

      this.setState({
        tasks: tasksArr
      })
    })

  }

  handleStatusChange(id) {
    let task = this.state.tasks.filter(task => {
      return task.id === id
    })

    task[0].completed = !task[0].completed;

    fbase.database().ref('tasks/' + id).set(task[0]);
  }

  deleteTask(id) {
    fbase.database().ref('tasks').child(id).remove();
  }

  addTask(title) {
      let task = {
        title,
        id: this.nextId(),
        completed: false
      };

      fbase.database().ref('tasks/' + task.id).set(task);
  }

  nextId() {
    return ++this._nextId;
  }

  handleEdit(id, title) {
    let task = this.state.tasks.filter(task => {
      return task.id === id
    })

    task[0].title = title;
    
    fbase.database().ref('tasks/' + id).set(task[0]);
  }

  render() {
    return (
      <main>

        <Title title=' ' task={this.state.tasks}/>

        
        <section className="task-list">
          {this.state.tasks.map((task) => 
            <Task 
              key={task.id} 
              id={task.id} 
              task={task.title} 
              completed={task.completed}
              onDeleteTask={this.deleteTask}
              onStatusChange={this.handleStatusChange}
              onEdit={this.handleEdit}/>)
          }
        </section>
        
        <Form onAdd={this.addTask}/>

      </main>
    );
  }
}

// ReactMixin(App.prototype, ReactFireMixin);

export default App;



